.joinContainer{
    width: 370px;
    height: 400px;
    margin: 70px auto;
    text-align: center;
    background-color: rgba(255, 255, 255, 0.731);
    box-shadow: 0 0 6px rgb(106, 106, 106);
    border-radius: 5px;
}

.join{
    padding: 20px;
    font-family: 'DM Serif Text';
}

.joinDiv{
    padding: 3px;
}

.input{
    padding: 5px;
    border-radius: 4px;
    border: 1px solid rgb(150, 150, 150);
    font-family: 'DM Serif Text';
}

.label{
    opacity: 0;
}

.joinButton{
    margin: 3rem;
    padding: 3px;
    border-radius: 4px;
    transition: all 1s cubic-bezier(.42, .8, .82, 1);
}

.joinButton:hover{
    box-shadow: inset 0 0 3px rgb(105, 105, 105);
}

