.container{
    width: 370px;
    height: 400px;
    margin: 70px auto;
    text-align: center;
    background-color: rgba(255, 255, 255, 0.731);
    box-shadow: 0 0 6px rgb(106, 106, 106);
    border-radius: 5px;
}

.login{
    padding: 20px;
    font-family: 'DM Serif Text';
}

.idContainer{
    padding-top: 10px;
    padding-bottom: 10px;
}

.id{
    padding: 10px;
    box-shadow: 0 0 2px rgb(186, 186, 186);
    border-radius: 5px;
    border: 1px solid rgb(177, 177, 177);
    font-family: 'DM Serif Text';
}

.pwContainer{
    padding-top: 10px;
    padding-bottom: 20px;
}

.pw{
    padding: 10px;
    box-shadow: 0 0 2px rgb(186, 186, 186);
    border-radius: 5px;
    border: 1px solid rgb(177, 177, 177);
    font-family: 'DM Serif Text';
}

.submit{
    font-family: 'DM Serif Text';
    border-radius: 5px;
    padding: 10px 10px 0 10px;
    transition: all 1s cubic-bezier(0.25, 0.8, 0.45, 1);
    text-align: center;
}

.submit:hover{
    box-shadow: inset 0 0 4px rgb(167, 167, 167);
}

.forgot{
    padding: 0;
    font-size: 13px;
    font-family: 'DM Serif Text';
}

.joinInLogin{
    padding-top: 90px;
    padding-left: 290px;
}