@import url('https://fonts.googleapis.com/css2?family=DM+Serif+Text:ital@0;1&display=swap');

.loading{
    text-align: center;
    font-family: 'DM Serif Text';
    font-size: 30px;
}

.summaryContainer{
    display: flex;
    justify-content: center;
    padding-top: 20px;
}

.summary{
    padding-left: 40px;
}

.explainPicture{
    font-family: 'DM Serif Text';
    text-align: center;
    padding: 0;
}

.title{
    font-family: 'DM Serif Text';
}

.genres{
    display: flex;
    font-size: 20px;
}

.bar{
    padding-right: 5px;
}

.mood{
    display: flex;
    list-style: none;
    padding-right: 5px;
    font-family: 'DM Serif Text';
    font-weight: bold;
}

.image{
    transition: all 2s cubic-bezier(.25, .8, .45, 1);
}

.image:hover{
    box-shadow: 0 0 6px black;
}

.response{
    display: flex;
    width: 180px;
    align-items: center;
    justify-content: space-around;
    font-family: 'DM Serif Text';
    padding-left: 40px;
}

.heart{
    color: #ff4f4f
}

.stars{
    color: #fbbf28;
}

.runtime{
    font-family: 'DM Serif Text';
}